mn;\n      align-items: center;\n      margin-bottom: 8px;\n      flex: 1;\n\n      .lawsuit-description__info-title {\n        text-align: left;\n        font-size: 14px;\n        color: #424242;\n      }\n\n      .lawsuit-description__info-text {\n        text-transform: capitalize;\n        text-align: center;\n        word-break: keep-all;\n        font-size: 14px;\n        color: #a3a3a3;\n      }\n\n      .lawsuit-description__info-download {\n        padding: 0px;\n        justify-content: flex-start;\n\n        .el-link--inner {\n          .lawsuit-description__document-download {\n            height: 16px;\n          }\n        }\n      }\n    }\n  }\n\n  .lawsuit-description__container-parties {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n\n    @media (max-width: 900px) {\n      flex-direction: column;\n      gap: 18px;\n    }\n\n    .lawsuit-description__container-party {\n\n      .lawsuit-description__party-title {\n        font-size: 14px;\n        font-weight: 700;\n        color: $--color-text-primary;\n      }\n\n      .lawsuit-description__party-collapse {\n        margin-left: 8px;\n        .lawsuit-description__party-items {\n          .lawsuit-description__party-name {\n            font-size: 12px;\n            line-height: 14px;\n            font-weight: 600;\n            color: $--color-text-primary;\n          }\n\n          .lawsuit-description__party-list {\n            padding-left: 20px;\n\n            .lawsuit-description__party-info {\n              font-size: 12px;\n              line-height: 14px;\n              color: $--color-text-secondary;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !defau